@import url(https://fonts.googleapis.com/css2?family=Merriweather+Sans:wght@400;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.note-list{width:700px;display:flex;flex-direction:column;align-items:center}.note-wrapper{width:100%;display:flex;justify-content:center}.note-container{width:500px;min-height:100px;background-color:white;border-radius:27px;padding:21px 51px;box-sizing:border-box;margin-top:20px;position:relative}@media only screen and (max-width: 600px){.note-container{width:340px}}.note-text{color:#34495E;outline:none;word-break:break-all}.note-mark,.note-delete,.note-edit{cursor:pointer;display:flex;justify-content:center;align-items:center;font:inherit;width:100px;height:40px;background-color:white;border:2px solid #34495E;border-radius:27px;margin-left:10px;outline:none;transition:ease-in-out 0.3s;padding:0;box-sizing:border-box}.note-edit{text-decoration:none;color:#34495E}.note-mark-marked{background-color:#34495E;color:white}.button-container{margin-top:20px;display:flex;width:100%;justify-content:flex-end}

.note-form{margin:0;width:500px;display:flex;flex-direction:column;align-items:center;justify-content:flex-start}@media only screen and (max-width: 600px){.note-form{width:340px}}.note-form__heading{font-family:Open Sans;font-style:normal;font-weight:bold;font-size:60px;line-height:87px;color:#34495E;margin-top:0}.note-form__input{font-family:inherit;font-size:inherit;outline:none;resize:unset;border:none;background-color:#34495E;color:white;white-space:pre-wrap;width:100%;height:200px;border-radius:27px;padding:21px 51px;box-sizing:border-box}.note-form__input::-webkit-input-placeholder{color:rgba(255,255,255,0.5)}.note-form__input:-ms-input-placeholder{color:rgba(255,255,255,0.5)}.note-form__input::-ms-input-placeholder{color:rgba(255,255,255,0.5)}.note-form__input::placeholder{color:rgba(255,255,255,0.5)}.note-form__button-container{height:55px;width:100%;margin-top:20px;display:flex;justify-content:center}.note-form__button{cursor:pointer;display:flex;justify-content:center;align-items:center;font-family:Open Sans;font-style:normal;font-weight:800;font-size:14px;line-height:19px;background-color:transparent;outline:none;width:200px;height:55px;color:#34495E;border:2px solid #34495E;box-sizing:border-box;border-radius:27px;transition:ease 0.5s}.note-form__button>*{transition:ease 0.5s}.note-form__button:hover{color:white;background-color:#34495E}.note-form__button:hover>*{fill:white}.note-form__button:active{background-color:transparent}

.app{display:flex;max-width:1024px;width:100vw;padding:30px;flex-direction:column;align-items:center;font-family:'Merriweather Sans', sans-serif}@media only screen and (max-width: 1024px){.app{padding:0;padding-bottom:30px;padding-top:30px}}.app-wrapper{display:flex;flex-direction:column;align-items:center;background:#ECF0F1;min-height:100vh}.header{width:100%;font-weight:800;font-size:20px;line-height:15px;letter-spacing:0.7px;color:#34495E;text-transform:uppercase;text-decoration:none;margin-bottom:40px}@media only screen and (max-width: 1024px){.header{text-align:center}}

